html {
  scroll-behavior: smooth;
}

[id] {
  scroll-margin-top: 80px;
}

/* ── Scroll-triggered animations ── */

.animate-fade-up {
  opacity: 0;
  transform: translateY(32px);
  transition: opacity 0.7s cubic-bezier(0.16, 1, 0.3, 1),
              transform 0.7s cubic-bezier(0.16, 1, 0.3, 1);
}
.animate-fade-up.visible {
  opacity: 1;
  transform: translateY(0);
}

.animate-fade-left {
  opacity: 0;
  transform: translateX(-40px);
  transition: opacity 0.7s cubic-bezier(0.16, 1, 0.3, 1),
              transform 0.7s cubic-bezier(0.16, 1, 0.3, 1);
}
.animate-fade-left.visible {
  opacity: 1;
  transform: translateX(0);
}

.animate-fade-right {
  opacity: 0;
  transform: translateX(40px);
  transition: opacity 0.7s cubic-bezier(0.16, 1, 0.3, 1),
              transform 0.7s cubic-bezier(0.16, 1, 0.3, 1);
}
.animate-fade-right.visible {
  opacity: 1;
  transform: translateX(0);
}

.animate-scale-in {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 0.6s cubic-bezier(0.16, 1, 0.3, 1),
              transform 0.6s cubic-bezier(0.16, 1, 0.3, 1);
}
.animate-scale-in.visible {
  opacity: 1;
  transform: scale(1);
}

.animate-fade-in {
  opacity: 0;
  transition: opacity 0.8s ease;
}
.animate-fade-in.visible {
  opacity: 1;
}

/* ── Stagger delays ── */
.delay-1 { transition-delay: 100ms; }
.delay-2 { transition-delay: 200ms; }
.delay-3 { transition-delay: 300ms; }
.delay-4 { transition-delay: 400ms; }
.delay-5 { transition-delay: 500ms; }
.delay-6 { transition-delay: 600ms; }
.delay-7 { transition-delay: 700ms; }

/* ── Hero entrance (plays on load, no IO needed) ── */
@keyframes hero-slide-up {
  from { opacity: 0; transform: translateY(40px); }
  to   { opacity: 1; transform: translateY(0); }
}
@keyframes hero-fade {
  from { opacity: 0; }
  to   { opacity: 1; }
}
.hero-anim-1 { animation: hero-slide-up 0.8s cubic-bezier(0.16, 1, 0.3, 1) 0.1s both; }
.hero-anim-2 { animation: hero-slide-up 0.8s cubic-bezier(0.16, 1, 0.3, 1) 0.25s both; }
.hero-anim-3 { animation: hero-slide-up 0.8s cubic-bezier(0.16, 1, 0.3, 1) 0.4s both; }
.hero-anim-4 { animation: hero-slide-up 0.8s cubic-bezier(0.16, 1, 0.3, 1) 0.55s both; }
.hero-anim-5 { animation: hero-fade 1s ease 0.7s both; }

/* ── Navbar slide-down entrance ── */
@keyframes nav-slide-down {
  from { opacity: 0; transform: translateY(-100%); }
  to   { opacity: 1; transform: translateY(0); }
}
.nav-entrance { animation: nav-slide-down 0.5s cubic-bezier(0.16, 1, 0.3, 1) 0.05s both; }

/* ── Phone mockup float ── */
@keyframes float {
  0%, 100% { transform: translateY(0); }
  50%      { transform: translateY(-8px); }
}
.animate-float {
  animation: float 4s ease-in-out infinite;
}

/* ── Typing cursor blink ── */
@keyframes blink {
  0%, 100% { opacity: 1; }
  50%      { opacity: 0; }
}
.animate-blink {
  animation: blink 1s step-end infinite;
}

/* ── Pulse ring for notification dot ── */
@keyframes pulse-ring {
  0%   { transform: scale(1); opacity: 1; }
  100% { transform: scale(2); opacity: 0; }
}
.animate-pulse-ring {
  animation: pulse-ring 1.5s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

/* ── Count up number animation ── */
@keyframes count-reveal {
  from { opacity: 0; transform: translateY(8px); }
  to   { opacity: 1; transform: translateY(0); }
}
.animate-count {
  animation: count-reveal 0.5s ease both;
}

